<template>
  <div class="form__counter" :class="isCartCounter && 'product__counter'">
    <button
      type="button"
      aria-label="Убрать один товар"
      :style="{ cursor: currentAmount > 1 ? 'pointer' : 'not-allowed' }"
      @click.prevent="subsAmount"
      :disabled="currentAmount < 2"
    >
      <svg
        :width="isCartCounter ? 10 : 12"
        :height="isCartCounter ? 10 : 12"
        fill="currentColor"
      >
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>
    <label>
      <input
        id="product-count"
        type="number"
        min="1"
        :max="maxAmount"
        v-model.number.lazy="currentAmount"
        @keydown="onMinusKeyDisable"
      />
    </label>
    <button
      type="button"
      aria-label="Добавить один товар"
      style="cursor: pointer"
      :style="{
        cursor: currentAmount >= maxAmount ? 'not-allowed' : 'pointer',
      }"
      @click.prevent="addAmount"
      :disabled="currentAmount >= maxAmount"
    >
      <svg
        :width="isCartCounter ? 10 : 12"
        :height="isCartCounter ? 10 : 12"
        fill="currentColor"
      >
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
import { MAX_PRODUCT_AMOUNT } from "@/config";

export default {
  name: "ProductCounter",
  props: {
    isCartCounter: {
      type: Boolean,
      required: false,
    },
    value: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      maxAmount: MAX_PRODUCT_AMOUNT,
    };
  },
  computed: {
    currentAmount: {
      get() {
        return this.value;
      },
      set(value) {
        if (value <= MAX_PRODUCT_AMOUNT && value > 0)
          this.$emit("input", value);
      },
    },
  },
  methods: {
    addAmount() {
      this.currentAmount < MAX_PRODUCT_AMOUNT && this.currentAmount++;
    },
    subsAmount() {
      this.currentAmount > 1 && this.currentAmount--;
    },
    onMinusKeyDisable(event) {
      if (event.key === "-") {
        event.preventDefault();
      }
    },
  },
};
</script>

<style scoped>
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type="number"] {
  -moz-appearance: textfield;
}
</style>
