<template>
  <div class="page__wrapper">
    <BaseHeader />
    <main class="content container">
      <div class="content__top">
        <div class="content__row">
          <h1 class="content__title">Каталог</h1>
          <span class="content__info">
            {{ productsCount }}
            {{ "товар" | normalizeWord(productsCount) }}</span
          >
        </div>
      </div>

      <div class="content__catalog">
        <ProductFilter
          :seasons="seasons"
          :categories="categories"
          :materials="materials"
          :colors="colors"
          :isFilterLoading="isFilterLoading"
          @filter-update="handleFilterUpdate"
        />

        <section class="catalog with-preloader">
          <ul class="catalog__list" v-if="products.length">
            <ProductItem
              v-for="product of products"
              :product="product"
              :key="product.id"
            />
          </ul>

          <BasePagination
            :pageCurrent.sync="pageCurrent"
            :pagesCount="pagesTotal"
          />
          <BasePreloader v-if="catalogPreloaderVisible" />
        </section>
      </div>
    </main>
    <BaseFooter />
  </div>
</template>

<script>
import BaseFooter from "@/components/BaseFooter.vue";
import BaseHeader from "@/components/BaseHeader.vue";
import ProductItem from "@/components/ProductItem.vue";
import ProductFilter from "@/components/ProductFilter.vue";
import BasePagination from "@/components/BasePagination.vue";
import normalizeWord from "@/helpers/normalise-word";
import BasePreloader from "@/components/BasePreloader.vue";
import Api from "@/helpers/api";

export default {
  name: "MainPage",
  components: {
    BaseHeader,
    BaseFooter,
    ProductItem,
    BasePagination,
    BasePreloader,
    ProductFilter,
  },
  data() {
    return {
      catalogPreloaderVisible: false,
      products: [],
      seasons: [],
      materials: [],
      categories: [],
      colors: [],
      pageCurrent: 1,
      pagesTotal: 1,
      productsCount: 0,
      categoryId: 0,
      limit: 12,
      currentFilter: {},
      isFilterLoading: false,
    };
  },
  methods: {
    handleFilterUpdate(filter) {
      this.currentFilter = filter;
      this.loadProducts();
    },
    loadProducts() {
      this.catalogPreloaderVisible = true;
      this.isFilterLoading = true;
      new Api()
        .getProducts({
          page: this.pageCurrent,
          limit: this.limit,
          ...this.currentFilter,
        })
        .then((productsResponse) => {
          this.products = productsResponse.data.items;
          this.pagesTotal = productsResponse.data.pagination.pages;
          this.productsCount = productsResponse.data.pagination.total;
        })
        .catch(console.log)
        .finally(() => {
          this.catalogPreloaderVisible = false;
          this.isFilterLoading = false;
        });
    },
    loadData() {
      const api = new Api();
      this.isFilterLoading = true;
      Promise.all([
        !this.categories.length && api.getProductCategories(),
        !this.materials.length && api.getMaterials(),
        !this.seasons.length && api.getSeasons(),
        !this.colors.length && api.getColors(),
      ])
        .then(
          ([
            categoriesResponse,
            materialsResponse,
            seasonsResponse,
            colorsResponse,
          ]) => {
            this.categories = categoriesResponse.data.items;
            this.materials = materialsResponse.data.items;
            this.seasons = seasonsResponse.data.items;
            this.colors = colorsResponse.data.items;
          }
        )
        .catch(console.log)
        .finally(() => {
          this.isFilterLoading = false;
        });
    },
  },
  created() {
    const categoryId = this.$route?.params?.categoryId;
    if (categoryId) this.currentFilter.categoryId = categoryId;

    this.loadProducts();
    this.loadData();
  },
  watch: {
    pageCurrent() {
      this.loadProducts();
    },
  },
  filters: {
    normalizeWord,
  },
};
</script>

