<template>
  <aside class="filter">
    <form
      class="filter__form form"
      action="#"
      method="get"
      @submit.prevent="submitForm"
    >
      <fieldset class="form__block">
        <legend class="form__legend">Цена</legend>
        <label class="form__label form__label--price">
          <input
            class="form__input"
            type="number"
            name="min-price"
            placeholder="0"
            v-model.number="validPriceFrom"
            :disabled="isFilterLoading"
          />
          <span class="form__value">От</span>
        </label>
        <label class="form__label form__label--price">
          <input
            class="form__input"
            type="text"
            name="max-price"
            placeholder="250000"
            v-model.number="validPriceTo"
            :disabled="isFilterLoading"
          />
          <span class="form__value">До</span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Категория</legend>
        <label class="form__label form__label--select">
          <select
            class="form__select"
            type="number"
            name="category"
            v-model.number="currentCategoryId"
            :disabled="isFilterLoading"
          >
            <option value="0">Все категории</option>
            <option
              v-for="category in categories"
              :value="category.id"
              :key="category.id"
            >
              {{ category.title }}
            </option>
          </select>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Цвет</legend>
        <ul class="colors">
          <li class="colors__item" v-for="color in colors" :key="color.code">
            <label class="colors__label color-interactive">
              <input
                class="colors__radio sr-only"
                type="checkbox"
                name="color"
                :value="color.id"
                v-model.number="checkedColors"
                :disabled="isFilterLoading"
              />
              <span
                class="colors__value"
                :style="{ backgroundColor: color.code }"
                :title="color.title"
              >
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Материал</legend>
        <ul class="check-list">
          <li
            class="check-list__item"
            v-for="material in materials"
            :key="material.code"
          >
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="material"
                :value="material.id"
                v-model="checkedMaterials"
                :disabled="isFilterLoading"
              />
              <span class="check-list__desc">
                {{ material.title }}
                <span>({{ material.productsCount }})</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Коллекция</legend>
        <ul class="check-list">
          <li
            class="check-list__item"
            v-for="season in seasons"
            :key="season.code"
          >
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="collection"
                :value="season.id"
                v-model="checkedSeasons"
                :disabled="isFilterLoading"
              />
              <span class="check-list__desc">
                {{ season.title }}
                <span>({{ season.productsCount }})</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <button
        class="filter__submit button button--primery"
        type="submit"
        :disabled="isFilterLoading"
      >
        <span>Применить</span>
      </button>
      <button
        class="filter__reset button button--second"
        type="button"
        @click.prevent="resetForm"
        :disabled="isFilterLoading"
      >
        Сбросить
      </button>
    </form>
  </aside>
</template>

<script>
import { MAX_FILTER_PRICE } from "@/config";

export default {
  name: "ProductFilter",
  props: ["seasons", "materials", "categories", "colors", "isFilterLoading"],
  data() {
    return {
      currentPriceFrom: null,
      currentPriceTo: null,
      currentCategoryId: 0,
      checkedSeasons: [],
      checkedMaterials: [],
      checkedColors: [],
    };
  },
  methods: {
    submitForm() {
      if (this.currentPriceTo < this.currentPriceFrom) {
        this.currentPriceTo = MAX_FILTER_PRICE;
      }
      if (this.currentPriceFrom > this.currentPriceTo) {
        this.currentPriceFrom = 0;
      }
      const result = {
        categoryId: this.currentCategoryId,
        materialIds: this.checkedMaterials,
        seasonIds: this.checkedSeasons,
        colorIds: this.checkedColors,
        minPrice: this.validPriceFrom,
        maxPrice: this.validPriceTo,
      };
      this.$emit("filter-update", result);
    },
    resetForm() {
      this.currentCategoryId = 0;
      this.checkedMaterials = [];
      this.checkedSeasons = [];
      this.checkedColors = [];
      this.validPriceFrom = null;
      this.validPriceTo = MAX_FILTER_PRICE;
      this.$emit("filter-update", {});
    },
  },
  computed: {
    validPriceFrom: {
      get() {
        return this.currentPriceFrom;
      },
      set(value) {
        if (value > 0 && value < MAX_FILTER_PRICE) {
          this.currentPriceFrom = value;
        }
      },
    },
    validPriceTo: {
      get() {
        return this.currentPriceTo;
      },
      set(value) {
        if (value > 0 && value < MAX_FILTER_PRICE) {
          this.currentPriceTo = value;
        }
      },
    },
  },
  created() {
    const categoryId = this.$route?.params?.categoryId;
    if (categoryId) this.$data.currentCategoryId = categoryId;
  },
};
</script>

<style>
.colors__label {
  opacity: 0.8;
  transition: 0.15s ease-in opacity;
  background-color: lightcyan;
}
.colors__label:has(.colors__radio:checked) {
  opacity: 1;
}
.colors__label:focus .colors__value::before,
.colors__label:hover .colors__value::before,
.colors__radio:checked ~ .colors__value::before {
  border-color: #000;
}
</style>
