export default function getPagesArrIndex(pagesArrs, currentPage) {
  if (typeof currentPage !== "number") {
    throw new TypeError("getPagesArrIndex: сurrentPage should be a number!");
  }
  let i = null;
  pagesArrs.forEach((arr, index) => {
    if (arr.includes(currentPage)) {
      i = index;
    }
  });
  return i;
}
