import { API_DELAY, BASE_API_URL } from "@/config";
import axios from "axios";

export default class Api {
  static _instance = {};
  constructor(apiUrl = BASE_API_URL) {
    this._apiUrl = apiUrl;
    this._userAccessKey = null;
    this._axios = axios.create({
      baseURL: BASE_API_URL,
      headers: "accept: application/json",
    });
    if (!Api._instance[this._apiUrl]) {
      Api._instance[this._apiUrl] = this;
    }
    return Api._instance[this._apiUrl];
  }

  async getColors() {
    return this._handleDelayedResponse(() => this._axios.get(`/api/colors`));
  }

  async getDeliveries() {
    return this._handleDelayedResponse(() =>
      this._axios.get(`/api/deliveries`)
    );
  }

  async getMaterials() {
    return this._handleDelayedResponse(() => this._axios.get(`/api/materials`));
  }

  async createOrder({
    phone,
    name,
    address,
    email,
    deliveryTypeId,
    paymentTypeId,
    comment,
  }) {
    this._checkAccessKeyLSExist();
    return this._handleDelayedResponse(() =>
      this._axios.post(
        `/api/orders`,
        { phone, name, address, email, deliveryTypeId, paymentTypeId, comment },
        {
          params: {
            userAccessKey: this._userAccessKey,
          },
        }
      )
    );
  }

  async getOrderById(orderId) {
    this._checkAccessKeyLSExist();
    return this._handleDelayedResponse(() =>
      this._axios.get(`/api/orders/${orderId}`, {
        params: {
          userAccessKey: this._userAccessKey,
        },
      })
    );
  }

  async getPaymentsById(deliveryTypeId) {
    return this._handleDelayedResponse(() =>
      this._axios.get(`/api/payments/${deliveryTypeId}`)
    );
  }

  async getProductCategories() {
    return this._handleDelayedResponse(() =>
      this._axios.get(`/api/productCategories`)
    );
  }

  async getProducts({
    categoryId = 0,
    materialIds = [],
    seasonIds = [],
    colorIds = [],
    page = 1,
    limit = 12,
    minPrice = 0,
    maxPrice = 0,
  } = {}) {
    return this._handleDelayedResponse(() =>
      this._axios.get(`/api/products`, {
        params: {
          categoryId,
          materialIds,
          seasonIds,
          colorIds,
          page,
          limit,
          minPrice,
          maxPrice,
        },
      })
    );
  }

  async getProductById(productId) {
    return this._handleDelayedResponse(() =>
      this._axios.get(`/api/products/${productId}`)
    );
  }

  async getSeasons() {
    return this._handleDelayedResponse(() => this._axios.get(`/api/seasons`));
  }

  async getCartProducts() {
    this._checkAccessKeyLSExist();
    return this._handleDelayedResponse(() =>
      this._axios.get(`/api/baskets`, {
        params: {
          userAccessKey: this._userAccessKey,
        },
      })
    );
  }

  async createCartProduct({ productId, colorId, sizeId, amount }) {
    this._checkAccessKeyLSExist();
    return this._handleDelayedResponse(() =>
      this._axios.post(
        `/api/baskets/products`,
        {
          productId,
          colorId,
          sizeId,
          quantity: amount,
        },
        {
          params: {
            userAccessKey: this._userAccessKey,
          },
        }
      )
    );
  }

  async updateCartProductAmount({ itemId, newAmount }) {
    this._checkAccessKeyLSExist();
    return this._handleDelayedResponse(() =>
      this._axios.put(
        `/api/baskets/products`,
        {
          basketItemId: itemId,
          quantity: newAmount,
        },
        {
          params: { userAccessKey: this._userAccessKey },
        }
      )
    );
  }

  async deleteCartProduct(itemId) {
    this._checkAccessKeyLSExist();
    return this._handleDelayedResponse(() =>
      this._axios.delete(`/api/baskets/products`, {
        data: {
          basketItemId: itemId,
        },
        params: {
          userAccessKey: this._userAccessKey,
        },
      })
    );
  }

  async getUserAccessKey() {
    return this._handleDelayedResponse(() =>
      this._axios(`/api/users/accessKey`)
    );
  }

  async _checkAccessKeyLSExist() {
    const userAccessKey = localStorage.getItem("user-access-key");
    if (userAccessKey) {
      this._userAccessKey = userAccessKey;
    } else {
      const response = await this.getUserAccessKey();
      if (response.status === 200 && response?.data?.accessKey) {
        localStorage.setItem("user-access-key", response.data.accessKey);
        this._userAccessKey = response.data.accessKey;
      }
    }
  }

  async _handleDelayedResponse(func) {
    return new Promise((res, rej) => {
      setTimeout(async () => {
        try {
          const response = await func();
          res(response);
        } catch (err) {
          rej(err);
        }
      }, API_DELAY);
    });
  }
}
