<template>
  <li class="cart__item product with-item-preloader">
    <div class="product__pic">
      <img
        class="product__img"
        :src="product.imgUrl"
        width="120"
        height="120"
        :alt="product.title"
      />
    </div>
    <h3 class="product__title">{{ product.title }}</h3>

    <p class="product__info product__info--color">
      Цвет:
      <span>
        <i
          :style="{
            backgroundColor: product.color.code,
            outline: '1px solid lightblue',
          }"
        ></i>
        {{ product.color.title }}
      </span>

      Размер:
      <span>
        <b class="product__size">
          {{ product.size.title }}
        </b>
      </span>
    </p>
    <span class="product__code"> Артикул: {{ product.id }} </span>

    <ProductCounter :is-cart-counter="true" v-model.number="amount" />

    <b class="product__price"> {{ multAmount }} ₽ </b>

    <button
      class="product__del button-del interactive"
      type="button"
      aria-label="Удалить товар из корзины"
      @click.prevent="deleteItem"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
    <BasePreloader v-if="product.isLoading" is-item-preloader="true" />
  </li>
</template>

<script>
import BasePreloader from "./BasePreloader.vue";
import ProductCounter from "./ProductCounter.vue";

export default {
  name: "CartItem",
  components: {
    ProductCounter,
    BasePreloader,
  },
  props: ["product"],
  data() {
    return {};
  },
  methods: {
    deleteItem() {
      this.$store.dispatch("deleteCartProduct", this.product.itemId);
    },
  },
  computed: {
    amount: {
      get() {
        return this.product.amount;
      },
      set(value) {
        this.$store.dispatch("updateCartProductAmount", {
          itemId: this.product.itemId,
          newAmount: value,
        });
      },
    },
    multAmount() {
      return this.product.amount * this.product.price;
    },
  },
};
</script>

<style lang="scss" scoped>
.product__img {
  width: 120px;
  height: 120px;
  object-fit: contain;
}
.product__info span:not(:last-child) {
  margin-right: 10px;
}
.product__info span:last-of-type {
  padding-left: 5px;
}

.interactive {
  cursor: pointer;
  transition: 0.15s ease-in color, 0.15s ease-in transform;

  &:focus-visible:not(:active),
  &:hover:not(:focus):not(:active) {
    transform: scale(1.1);
    color: #e02d71;
  }
  &:active {
    opacity: 0.3;
  }
}
</style>
