<template>
  <div class="page__wrapper">
    <BaseHeader />
    <main class="content container with-preloader">
      <div class="content__top" v-if="product">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <RouterLink class="breadcrumbs__link" :to="{ name: 'home' }">
              Каталог
            </RouterLink>
          </li>
          <li class="breadcrumbs__item">
            <RouterLink
              class="breadcrumbs__link"
              :to="{
                name: 'home',
                params: { categoryId: this.product?.category?.id },
              }"
            >
              {{ product.category.title }}
            </RouterLink>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link"> {{ product.title }} </a>
          </li>
        </ul>
      </div>
      <section class="item" v-if="product">
        <div class="item__pics pics">
          <div class="pics__wrapper">
            <img
              width="570"
              height="570"
              :src="firstImage || '/img/product-placeholder.jpg'"
              :alt="product.title"
            />
          </div>
          <ul class="pics__list" v-if="otherImages">
            <li
              class="pics__item"
              v-for="(url, index) in otherImages"
              :key="url"
            >
              <a
                href=""
                class="pics__link pics__link--current"
                @click.prevent="mixImages(index + 1)"
              >
                <img width="98" height="98" :src="url" :alt="product.title" />
              </a>
            </li>
          </ul>
        </div>

        <div class="item__info">
          <span class="item__code">Артикул: {{ product.id }}</span>
          <h2 class="item__title">{{ product.title }}</h2>
          <div class="item__form">
            <form
              class="form"
              action="#"
              method="POST"
              @submit.prevent="submit"
            >
              <div class="item__row item__row--center">
                <ProductCounter
                  :is-cart-counter="false"
                  v-model.number="currentAmount"
                />
                <b class="item__price"> {{ multAmount | numberFormat }} ₽ </b>
              </div>

              <div class="item__row">
                <fieldset class="form__block">
                  <legend class="form__legend">Цвет</legend>
                  <ul class="colors colors--black">
                    <li
                      class="colors__item"
                      v-for="item in product.colors"
                      :key="item.color.id"
                    >
                      <label class="colors__label">
                        <input
                          class="colors__radio sr-only"
                          type="radio"
                          name="color-item"
                          :value="item.color.id"
                          v-model="currentColorId"
                        />
                        <span
                          class="colors__value"
                          :style="{ backgroundColor: item.color.code }"
                        >
                        </span>
                      </label>
                    </li>
                  </ul>
                </fieldset>

                <fieldset class="form__block">
                  <legend class="form__legend">Размер</legend>
                  <label
                    class="form__label form__label--small form__label--select"
                  >
                    <select
                      class="form__select"
                      type="text"
                      name="sizes"
                      v-model="currentSizeId"
                    >
                      <option
                        v-for="size in product.sizes"
                        :key="size.id"
                        :value="size.id"
                      >
                        {{ size.title }}
                      </option>
                    </select>
                  </label>
                </fieldset>
              </div>

              <button class="item__button button button--primery" type="submit">
                В корзину
              </button>
            </form>
          </div>
        </div>

        <div class="item__desc">
          <ul class="tabs">
            <li class="tabs__item">
              <a
                class="tabs__link"
                :class="{ 'tabs__link--current': infoBlockToShow === 1 }"
                :href="infoBlockToShow !== 1 ? '#' : false"
                @click.prevent="setInfoBlockToShow(1)"
              >
                Информация о товаре
              </a>
            </li>
            <li class="tabs__item">
              <a
                class="tabs__link"
                :class="{ 'tabs__link--current': infoBlockToShow === 2 }"
                :href="infoBlockToShow !== 2 ? '#' : false"
                @click.prevent="setInfoBlockToShow(2)"
              >
                Доставка и возврат
              </a>
            </li>
          </ul>

          <div class="item__content" v-show="infoBlockToShow === 1">
            <h3>Состав:</h3>
            <p>
              60% хлопок<br />
              30% полиэстер<br />
            </p>
            <h3>Уход:</h3>
            <p>
              Машинная стирка при макс. 30ºC короткий отжим<br />
              Гладить при макс. 110ºC<br />
              Не использовать машинную сушку<br />
              Отбеливать запрещено<br />
              Не подвергать химчистке<br />
            </p>
          </div>

          <div class="item__content" v-show="infoBlockToShow === 2">
            <h3>Доставка:</h3>
            <p>
              1. Курьерская доставка по Москве и Московской области – 290 ₽<br />
              2.Самовывоз из магазина. Список и адреса магазинов Вы можете
              посмотреть здесь<br />
            </p>
            <h3>Возврат:</h3>
            <p>
              Любой возврат должен быть осуществлен в течение Возвраты в
              магазине БЕСПЛАТНО.<br />
              Вы можете вернуть товары в любой магазин. Магазин должен быть
              расположен в стране, в которой Вы осуществили покупку. БЕСПЛАТНЫЙ
              возврат в Пункт выдачи заказов.<br />
              Для того чтобы вернуть товар в одном из наших Пунктов выдачи
              заказов, позвоните по телефону 8 800 600 90 09<br />
            </p>
          </div>
        </div>
      </section>
      <BasePreloader v-if="!this.product" />
    </main>
    <BaseFooter />
  </div>
</template>

<script>
import BaseFooter from "@/components/BaseFooter.vue";
import BaseHeader from "@/components/BaseHeader.vue";
import BasePreloader from "@/components/BasePreloader.vue";
import ProductCounter from "@/components/ProductCounter.vue";
import Api from "@/helpers/api";
import numberFormat from "@/helpers/number-format";
import { RouterLink } from "vue-router";
import { mapActions } from "vuex";

export default {
  name: "ProductPage",
  components: {
    BaseHeader,
    BaseFooter,
    BasePreloader,
    ProductCounter,
    RouterLink,
  },
  data() {
    return {
      product: null,
      currentColorId: null,
      currentSizeId: null,
      currentAmount: 1,
      infoBlockToShow: 1,
    };
  },
  methods: {
    ...mapActions(["createCartProduct"]),
    onMinusKeyDisable(event) {
      if (event.key === "-") {
        event.preventDefault();
      }
    },
    submit() {
      this.createCartProduct({
        product: this.product,
        colorId: this.currentColorId,
        sizeId: this.currentSizeId,
        amount: this.currentAmount,
      });
    },
    setInfoBlockToShow(num) {
      this.infoBlockToShow = num;
    },
    addAmount() {
      this.currentAmount < 99 && this.currentAmount++;
    },
    subsAmount() {
      this.currentAmount > 1 && this.currentAmount--;
    },
    mixImages(colorId, imageIndex) {
      const pickedImage = this.images[colorId][imageIndex];
      const filtred = this.images[colorId].filter(
        (img, index) => index !== imageIndex
      );
      filtred.unshift(pickedImage);
      this.images[colorId] = filtred;
    },
  },
  watch: {
    currentAmount(value) {
      if (value > 0 && value < 99) {
        this.currentAmount = value;
      }
    },
  },
  computed: {
    multAmount() {
      return this.currentAmount * this.product.price;
    },
    images() {
      let imgs = {};
      if (this.product) {
        const colors = this.product?.colors;
        colors.forEach((it) => {
          if (Array.isArray(it?.gallery) && it?.gallery?.length)
            imgs[it.color.id] = it.gallery.map((img) => img.file.url);
        });
      }
      return imgs;
    },
    firstImage() {
      if (
        this.images[this.currentColorId] &&
        this.images[this.currentColorId].length
      )
        return this.images[this.currentColorId][0];
      return null;
    },
    otherImages() {
      if (
        this.images[this.currentColorId] &&
        this.images[this.currentColorId].length > 1
      )
        return this.images[this.currentColorId].slice(1);
      return null;
    },
  },
  created() {
    new Api()
      .getProductById(this.$route.params.id)
      .then((response) => {
        this.product = response.data;
        this.currentSizeId = this.product.sizes[0].id;
        this.currentColorId = this.product.colors[0].color.id;
      })
      .catch(console.log)
      .finally(() => {});
  },
  filters: {
    numberFormat,
  },
};
</script>
