import Api from "@/helpers/api";
const api = new Api();
let oldAmounts = [];
let timeouts = [];

function debounce(func, delay = 300) {
  if (timeouts.length) {
    timeouts.forEach((t) => clearTimeout(t));
  }
  timeouts.push(
    setTimeout(() => {
      func();
    }, delay)
  );
}

export default {
  state: {
    cartProducts: [],
    user: null,
    cartId: null,
    isCartLoading: false,
    isCartBlocked: false,
  },
  getters: {
    productsLength(state) {
      return state.cartProducts.length;
    },
    totalAmount(state) {
      return state.cartProducts.reduce((acc, curr) => {
        return acc + curr.price * curr.quantity;
      }, 0);
    },
    cartBlocked(state) {
      return state.isCartBlocked;
    },
    cartLoading(state) {
      return state.isCartLoading;
    },
    productsForCartPage(state) {
      return state.cartProducts.map((product) => ({
        color: product.color.color,
        imgUrl: product.color?.gallery?.length
          ? product.color.gallery[0].file.url
          : "img/product-placeholder.jpg",
        id: product.product.id,
        price: product.price,
        itemId: product.id,
        amount: product.quantity,
        size: product.size,
        title: product.product.title,
        isLoading: product.isLoading,
      }));
    },
  },
  mutations: {
    setIsCartBlocked(state, payload) {
      state.isCartBlocked = payload;
    },
    setItemIsLoading(state, payload) {
      state.cartProducts.find((it) => it.id === payload.id).isLoading =
        payload.isLoading;
    },
    setCartIsLoading(state, payload) {
      state.isCartLoading = payload;
    },
    setProducts(state, payload) {
      state.cartProducts = payload.map((p) => ({ ...p, isLoading: false }));
    },
    setUser(state, payload) {
      state.user = payload;
    },
    setCartId(state, payload) {
      state.cartId = payload;
    },
    addProduct(state, payload) {
      const finded = state.cartProducts.find(
        (it) =>
          it.product.id === payload.product.id &&
          it.color.id === payload.color.id &&
          it.size.id === payload.size.id
      );
      if (finded) {
        finded.id = payload.id;
        finded.quantity += payload.quantity;
      } else {
        state.cartProducts.push(payload);
      }
    },
    deleteProduct(state, payload) {
      state.cartProducts = state.cartProducts.filter(
        (product) => product.id !== payload
      );
    },
    updateAmount(state, payload) {
      const product = state.cartProducts.find(
        (product) => product.id === payload.id
      );
      if (product) {
        product.quantity = payload.amount;
      }
    },
  },
  actions: {
    async getCartProducts({ commit }) {
      commit("setCartIsLoading", true);
      try {
        const response = await api.getCartProducts();
        commit("setProducts", response.data.items);
        commit("setUser", response.data.user);
        commit("setCartId", response.data.id);
        return response;
      } catch (error) {
        console.log("eeee", error);
      } finally {
        commit("setCartIsLoading", false);
      }
    },

    async createCartProduct({ commit }, { product, colorId, sizeId, amount }) {
      const newProduct = {
        id: Date.now(),
        product,
        color: product.colors.find((it) => it.color.id === colorId),
        size: product.sizes.find((it) => it.id === sizeId),
        quantity: amount,
        price: product.prise,
      };
      commit("addProduct", newProduct);
      try {
        const response = await api.createCartProduct({
          productId: product.id,
          colorId,
          sizeId,
          amount,
        });
        commit("setProducts", response.data.items);
      } catch (error) {
        console.log("eeee", error);
        commit("deleteProduct", newProduct.id);
      }
    },

    async updateCartProductAmount({ commit, state }, { itemId, newAmount }) {
      oldAmounts.push(
        state.cartProducts.find((it) => it.id === itemId).quantity
      );
      commit("updateAmount", { id: itemId, amount: newAmount });

      debounce(async () => {
        try {
          await api.updateCartProductAmount({ itemId, newAmount });
        } catch (error) {
          console.log("error", error);
          if (oldAmounts.length) {
            commit("updateAmount", {
              id: itemId,
              amount: oldAmounts.shift(),
            });
            oldAmounts.length = 0;
          }
        }
      });
    },

    async deleteCartProduct({ commit }, productId) {
      commit("setItemIsLoading", { id: productId, isLoading: true });
      debounce(async () => {
        try {
          await api.deleteCartProduct(productId);
          commit("deleteProduct", productId);
        } catch (error) {
          console.log("errr", error);
        }
      });
    },
  },
};
