export default function createPagesArr(pagesCount, pagesToShow = 4) {
  if (typeof pagesCount !== "number") {
    throw new TypeError("createPagesArr: pagesCount should be a number!");
  }
  if (typeof pagesToShow !== "number") {
    throw new TypeError("createPagesArr: pagesToShow should be a number!");
  }
  let pagePivot = 1;
  if (pagesCount <= pagesToShow + 1) {
    return [
      new Array(pagesToShow + 1)
        .fill(null)
        .map(() => (pagePivot <= pagesCount ? pagePivot++ : null))
        .filter((page) => !!page),
    ];
  }
  const arrsCount = Math.ceil(pagesCount / pagesToShow);
  let pagesArr = new Array(arrsCount).fill(new Array(pagesToShow).fill(null));

  pagesArr = pagesArr.map((pageArr) =>
    pageArr.map(() => (pagePivot <= pagesCount ? pagePivot++ : null))
  );
  return pagesArr.map((arr) => arr.filter((page) => !!page));
}
