<template>
  <ul class="catalog__pagination pagination" v-if="pagesCount">
    <li class="pagination__item">
      <a
        class="pagination__link pagination__link--arrow"
        :class="[{ 'pagination__link--disabled': pageCurrent === 1 }]"
        href="#"
        aria-label="Предыдущая страница"
        @click.prevent="updatePageCurrent(pageCurrent - 1, pageCurrent > 1)"
      >
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li class="pagination__item">
      <a
        class="pagination__link"
        href="#"
        v-show="isShowPrevDots"
        @click.prevent="updatePageCurrent(1, pageCurrent !== 1)"
      >
        1
      </a>
    </li>
    <li class="pagination__item" v-show="isShowPrevDots">
      <a
        class="pagination__link"
        href="#"
        @click.prevent="updatePageCurrent(pageCurrent - pagesCountToShow)"
      >
        ...
      </a>
    </li>
    <li
      class="pagination__item"
      v-for="page in pagesData.currentPagesArray"
      :key="page"
    >
      <a
        class="pagination__link"
        :class="[{ 'pagination__link--current': page === pageCurrent }]"
        :href="page !== pageCurrent ? '#' : false"
        @click.prevent="updatePageCurrent(page, page !== pageCurrent)"
      >
        {{ page }}
      </a>
    </li>
    <li class="pagination__item" v-show="isShowNextDots">
      <a
        class="pagination__link"
        href="#"
        @click.prevent="updatePageCurrent(pageCurrent + pagesCountToShow)"
      >
        ...
      </a>
    </li>
    <li class="pagination__item">
      <a
        class="pagination__link"
        href="#"
        v-show="isShowNextDots"
        @click.prevent="
          updatePageCurrent(pagesCount, pageCurrent !== pagesCount)
        "
      >
        {{ lastPageIndex }}
      </a>
    </li>
    <li class="pagination__item">
      <a
        class="pagination__link pagination__link--arrow"
        :class="[
          {
            'pagination__link--disabled': pageCurrent >= pagesCount,
          },
        ]"
        href="#"
        aria-label="Следующая страница"
        @click.prevent="
          updatePageCurrent(pageCurrent + 1, pageCurrent < pagesCount)
        "
      >
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>
import createPagesArr from "@/helpers/create-pages-arr";
import getPagesArrIndex from "@/helpers/get-pages-arr-index";

export default {
  name: "BasePagination",
  props: ["pageCurrent", "pagesCount"],
  data() {
    return {
      pagesCountToShow: 4,
    };
  },
  methods: {
    updatePageCurrent(value, allow = true) {
      let toPage;
      if (value < 1) {
        toPage = 1;
      } else if (value > this.pagesCount) {
        toPage = this.pagesCount;
      } else {
        toPage = value;
      }
      if (allow) this.$emit("update:pageCurrent", toPage);
    },
  },
  computed: {
    lastPageIndex() {
      return this.pagesCount;
    },

    isShowPrevDots() {
      return this.pagesCount === this.pagesCountToShow + 1
        ? this.pageCurrent > this.pagesCountToShow + 1
        : this.pageCurrent > this.pagesCountToShow;
    },
    isShowNextDots() {
      return (
        this.pagesCount > this.pagesCountToShow + 1 &&
        this.pagesData.currentArrIndex !== this.pagesData.pagesArrs.length - 1
      );
    },
    pagesData() {
      const pagesArrs = createPagesArr(this.pagesCount, this.pagesCountToShow);
      const currentArrIndex = getPagesArrIndex(pagesArrs, this.pageCurrent);
      const currentPagesArray = pagesArrs[currentArrIndex];
      return { pagesArrs, currentArrIndex, currentPagesArray };
    },
  },
};
</script>
